import React, { useState } from "react";
import { getManifest } from "src/api/missionManifests";
import Manifest, { getEmptyManifest } from "src/entities/Manifest";

const ManifestPage: React.FC = () => {
  const [rover, setRover] = useState("Curiosity");
  const [manifest, setManifest] = useState(getEmptyManifest());

  const handleSelectChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    setRover(event.target.value);
  };
  const handleButton = () => {
    getManifest(rover).then((apiResp) => {
      setManifest(apiResp);
    });
  };
  return (
    <div className='center'>
      <select value={rover} onChange={handleSelectChange}>
        <option value='Curiosity'>Curiosity</option>
        <option value='Opportunity'>Opportunity</option>
        <option value='Spirit'>Spirit</option>
      </select>
      <br />
      <button type='button' onClick={handleButton}>
        Случайная картинка
      </button>
      <br />
    </div>
  );
};

export default ManifestPage;
